<template>
  <div id="edit-note">
    <head-top title="编辑笔记">
      <span slot="right" class="head-right" @click="saveData">
        <i class="iconfont icon-finish"></i>
      </span>
    </head-top>

    <div class="main">

      <van-field readonly clickable placeholder="请选择课程" v-model="classHourName" @click="beforeShow">
      </van-field>
      <van-popup v-model="showPicker" position="bottom">
        <van-picker id="coursePicker" show-toolbar :columns="columns" :loading="selectLoading"
          get-container="#edit-note" @cancel="showPicker = false" @confirm="onConfirm"
          @change="handleChange">
        </van-picker>
      </van-popup>

      <van-field type="textarea" v-model="title" placeholder="笔记标题" autosize maxlength="50"
        show-word-limit>
      </van-field>

      <r-quill-editor ref="REditor" :is-class="false" :funType="funType" @change="editorChange"
        max-height="calc(50vh)">
      </r-quill-editor>
    </div>

  </div>
</template>

<script>
import RQuillEditor from "@/components/r-quill-editor";
import { ModulePicture } from "@/utils/enum";
// import { formatDate } from "@/utils/formatData";
import { emojValidator } from "@/utils/index";
export default {
  name: "",
  components: { RQuillEditor },
  props: {},
  data() {
    return {
      snID: this.$route.query ? this.$route.query.snID || "" : "",
      title: this.$route.query ? this.$route.query.title || "" : "",
      content: this.$route.query ? this.$route.query.content || "" : "",
      courseID: this.$route.query ? this.$route.query.courseID || "" : "",
      courseName: this.$route.query ? this.$route.query.courseName || "" : "",
      classHourID: this.$route.query ? this.$route.query.classHourID || "" : "",
      classHourName: this.$route.query
        ? this.$route.query.classHourName || ""
        : "",
      funType: ModulePicture.StudentNote,
      saveLoading: false,
      courseOptions: [{ text: "请选择课程", value: "" }],

      showPicker: false,
      columns: [
        {
          values: [],
          className: "column1",
          defaultIndex: 0
        },
        {
          values: [],
          className: "column2"
        }
      ],
      courseHours: [],
      selectLoading: false
    };
  },
  created() {
    this.loadAllCourse();
  },
  mounted() {
    if (this.$route.query && this.$route.query.content) {
      this.$refs["REditor"].initValue(this.$route.query.content);
    }
  },
  methods: {
    //加载学生课程
    loadAllCourse() {
      this.courseOptions = [{ text: "请选择课程", value: "" }];
      this.columns[0].values = [];
      this.$mainHttp.course
        .getStudentCourseList({ studentID: this.$store.getters.studentID })
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.sort(r => r["ClassDate"]).reverse();
            res.Datas.forEach(item => {
              let sub = {
                text: `${item.CourseName}`,
                value: item.CourseID
              };
              // this.courseOptions.push(sub);
              this.columns[0].values.push(sub);
            });
            this.loadAllClassHour(this.columns[0].values[0].value, null);
          }
        })
        .catch(err => {});
    },
    loadAllClassHour(courseID, picker) {
      let index = this.courseOptions.findIndex(r => r.courseID === courseID);
      if (index > -1) {
        this.columns[1].values = this.courseOptions[index].values;
        if (picker) picker.setColumnValues(1, this.courseOptions[index].values);
      } else {
        let values = [];
        this.selectLoading = true;
        this.$mainHttp.classHour
          .dataList({ courseID: courseID })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.sort(r => r["CreateTime"]).reverse();
              res.Datas.forEach(item => {
                values.push({
                  text: `${item.CHName}`,
                  value: item.CHID
                });
              });

              this.courseOptions.push({
                courseID: courseID,
                values: values
              });
              this.columns[1].values = values;
              if (picker) picker.setColumnValues(1, values);
            }
            this.selectLoading = false;
          })
          .catch(err => {
            this.selectLoading = false;
          });
      }
    },
    beforeShow() {
      if (this.courseName) {
        let index = this.columns[0].values.findIndex(
          r => r.text === this.courseName
        );
        if (index > -1) {
          this.loadDefaultClassHour(this.columns[0].values[index].value, index);
        } else {
          this.showPicker = true;
        }
      } else {
        this.showPicker = true;
      }
    },
    /**
     * 预加载对应的课程
     * @param courseID
     */
    loadDefaultClassHour(courseID, sub) {
      let index = this.courseOptions.findIndex(r => r.courseID === courseID);
      if (index > -1) {
        this.columns[1].values = this.courseOptions[index].values;
        this.showPicker = true;
      } else {
        let values = [];
        this.$mainHttp.classHour
          .dataList({ courseID: courseID })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.sort(r => r["CreateTime"]).reverse();
              res.Datas.forEach(item => {
                values.push({
                  text: `${item.CHName}`,
                  value: item.CHID
                });
              });

              this.courseOptions.push({
                courseID: courseID,
                values: values
              });
              this.columns[1].values = values;
              this.columns[0].defaultIndex = sub;
              this.columns[1].defaultIndex = values.findIndex(
                r => r.value === this.classHourID
              );
            }
            this.showPicker = true;
          })
          .catch(err => {
            this.showPicker = true;
          });
      }
    },
    saveData() {
      if (this.saveLoading) return;
      this.saveLoading = true;
      if (!this.classHourID) {
        this.$toast("请选择课程");
        this.saveLoading = false;
        return;
      }

      if (!this.title) {
        this.$toast("笔记标题不能为空");
        this.saveLoading = false;
        return;
      } else if (emojValidator(this.title)) {
        this.$toast("不支持表情字符");
        this.saveLoading = false;
        return;
      }

      if (!this.content) {
        this.$toast("笔记内容不能为空");
        this.saveLoading = false;
        return;
      } else if (emojValidator(this.content)) {
        this.$toast("不支持表情字符");
        this.saveLoading = false;
        return;
      }

      this.$mainHttp.note
        .postSave({
          snID: this.snID,
          ciID: this.courseID,
          chID: this.classHourID,
          studentID: this.$store.getters.studentID,
          title: this.title,
          content: this.content
        })
        .then(res => {
          if (res.Result) {
            this.$toast.success("编辑笔记成功");
            this.$router.push("/space/note");
          } else if (res.Info) {
            this.$toast(res.Info);
          }
          this.saveLoading = false;
        })
        .catch(err => {
          this.$toast("添加失败，请重试");
          this.saveLoading = false;
        });
    },

    editorChange(value) {
      this.content = value;
    },

    handleChange(picker, values) {
      this.loadAllClassHour(values[0].value, picker);
    },
    onConfirm(values) {
      this.classHourID = values[1].value;
      this.classHourName = values[1].text;
      this.showPicker = false;
    }
  }
};
</script>

<style lang='less' scoped>
#edit-note {
  width: 100%;
  height: 100%;
  background: #ebf0ef;
  .main {
    text-align: left;
    background: #ebf0ef;

    .tt {
      width: 92vw;
      padding-left: 4vw;
      padding-right: 4vw;
      color: #999;
      height: 40px;
      line-height: 40px;
      background: #fff;
      input {
        color: #999;
        height: 35px;
        line-height: 35px;
        width: 100%;
      }
    }

    /deep/ .van-dropdown-menu__title {
      width: 87%;
      margin-left: -5%;
      color: rgb(117, 117, 117);
      font-size: 12px;
    }
  }
}
</style>


